import axios from "axios"

const Get_Data="Get_Data"
const Get_Address="Get_Address"
const Get_Business="Get_Business"

export default{
    [Get_Data](context,id){
        context.commit("Get_Data",id)
    },
    [Get_Address]({commit}){
        axios.get("https://www.easy-mock.com/mock/5b4972414ddc075ff3d24f53/example/address")
        .then(re=>{
            commit("Get_Address",re)
        })
        .catch(error=>{
            console.log(error)
        })
    },
    [Get_Business]({commit}){
        axios.get("../static/json/business.json")
        .then(re=>{
            //console.log(re)
            commit("Get_Business",re)
        })
        .catch(error=>{
            console.log(error)
        })
    }
}



