<template>
    <div class="business">
        <div class="business-top">
            <img src="../../static/img/foot/hmj.jpg" alt="">
            <h1 class="title">
                <span>{{title}}</span>
                <img class="header-picture" src="" alt="">
            </h1>
            <p>{{time}}</p>
        </div>
        <div>
            <div v-for="(item, index) in discount" :key="index">
                <p>
                    <i>{{item.icon}}</i>
                    <span>{{item.title}}</span>
                </p>
            </div>
        </div>
    </div>
</template>

<script>
export default{
    name:"business",
    data() {
        return {
            title:"必胜客宅急送（中关村店）",
            time:"配送时间",
            hongbao:8,
            discount:[{
                "icon":"满",
                "title":"满100减20",
                
            },{
                "icon":"折",
                "title":"指定时间会员享受5折优惠",
            },{
                "icon":"返",
                "title":"实际支付50元返5元红包"
            }],
            intr:[
                {
                    "公告":"必胜客宅急送_会员狂欢季_经典美味3折起_天天有惊喜必胜客宅急送周年庆会员狂欢季,10/21至11/17每天都有惊喜,经典美味3折起,还有比萨11元限时秒杀,主食买一送一,半价套餐等超值优惠,嗨吃四周,划算到底!"
                }
            ],
        }
    },
}

</script>


<style lang="scss">
.business{
    background: #fff;
}
.business-top{
    .title{
        font-size:0.38rem;
        font-weight:700;
        color:#000;
    }
}

</style>


